const mutations = {
  SET_SKILLS(state, skills) {
    state.skills = skills;
  },
  TOGGLE_SKILL(state, skillId) {
    const skill = state.skills.find(s => s.id === skillId);
    if (skill) {
      skill.expanded = !skill.expanded;
    }
  }
};

const actions = {
  fetchSkills({ commit }) {
    // In a real app, this might be an API call
    commit('SET_SKILLS', state.skills);
  },
  toggleSkill({ commit }, skillId) {
    commit('TOGGLE_SKILL', skillId);
  }
};

const getters = {
  getAllSkills: state => state.skills
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
};
