<!-- src/components/BookItem.vue -->
<template>
  <div :class="['book-summary', { 'list-view': isListView, 'card-view': !isListView }]">
    <img v-if="book.image" :src="book.image" :alt="book.title + ' cover'" class="book-cover">
    <div v-else class="book-cover-placeholder">
      <i class="fas fa-book"></i>
    </div>
    <div v-if="isListView" class="book-info">
      <div class="book-title">{{ book.title }}</div>
      <div class="book-author">{{ book.author }}</div>
      <div v-if="book.progress > 0 && book.progress < 100" class="progress-container">
        <div class="progress-bar">
          <div class="progress" :style="{ width: `${book.progress}%` }"></div>
        </div>
        <span class="progress-text">{{ book.progress }}%</span>
      </div>
      <div v-if="book.dateRead" class="completion-date">
        Completed on: {{ formatDate(book.dateRead) }}
      </div>
      <div class="book-tags">
        {{ book.tags.join(', ') }}
      </div>
    </div>
    <div v-else-if="book.progress > 0 && book.progress < 100" class="progress-container">
      <div class="progress-bar">
        <div class="progress" :style="{ width: `${book.progress}%` }"></div>
      </div>
      <span class="progress-text">{{ book.progress }}%</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BookItem',
  props: {
    book: {
      type: Object,
      required: true
    },
    isListView: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    formatDate(dateString) {
      if (!dateString) return 'Not available'
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    }
  }
}
</script>

