<!-- src/views/Books.vue -->
<template>
  <div class="books content-container">
	<h1 class="books-title">Books</h1>
    <div class="view-toggle">
      View: 
      <button @click="toggleView" class="view-button">
        <i :class="['fas', isListView ? 'fa-th-large' : 'fa-list']"></i>
      </button>
    </div>

    <!-- Currently Reading Section -->
    <div v-if="currentlyReadingBooks.length > 0" class="book-section">
      <!--<h2 class="section-title">Currently Reading</h2>-->
      <ul :class="['book-list', { 'cover-view': !isListView }]">
        <li v-for="book in currentlyReadingBooks" :key="book.isbn" class="book-item">
          <BookItem :book="book" :isListView="isListView" @click="navigateToBookDetails(book.isbn)" />
        </li>
      </ul>
    </div>

    <!-- Read Section -->
    <div v-if="readBooks.length > 0" class="book-section">
      <h2 class="section-title">Read</h2>
      <div v-for="category in readCategories" :key="category" class="sub-category">
        <h3 class="sub-category-title">{{ category }}</h3>
        <ul :class="['book-list', { 'cover-view': !isListView }]">
          <li v-for="book in getBooksInCategory(category)" :key="book.isbn" class="book-item">
            <BookItem :book="book" :isListView="isListView" @click="navigateToBookDetails(book.isbn)" />
          </li>
        </ul>
      </div>
    </div>

    <!-- Want to Read Section -->
    <div v-if="wantToReadBooks.length > 0" class="book-section">
      <h2 class="section-title">Want to Read</h2>
      <ul :class="['book-list', { 'cover-view': !isListView }]">
        <li v-for="book in wantToReadBooks" :key="book.isbn" class="book-item">
          <BookItem :book="book" :isListView="isListView" @click="navigateToBookDetails(book.isbn)" />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'
import BookItem from '@/components/BookItem.vue'

export default {
  name: 'Books',
  components: { BookItem },
  setup() {
    const store = useStore()
    const books = ref([])
    const isListView = ref(true)

    onMounted(async () => {
      await store.dispatch('content/loadBooks')
      books.value = store.getters['content/getBooks']
    })

    const toggleView = () => {
      isListView.value = !isListView.value
    }

    const navigateToBookDetails = (isbn) => {
      console.log(`Navigating to book details for ISBN: ${isbn}`)
    }

    const currentlyReadingBooks = computed(() => 
      books.value.filter(book => book.shelf === 'currently-reading')
    )

    const readBooks = computed(() => 
      books.value.filter(book => book.shelf === 'read')
    )

    const wantToReadBooks = computed(() => 
      books.value.filter(book => book.shelf === 'to-read')
    )

    const readCategories = computed(() => {
      const categories = new Set()
      readBooks.value.forEach(book => {
        book.tags.forEach(tag => {
          if (tag !== 'read') categories.add(tag)
        })
      })
      return Array.from(categories).sort()
    })

    const getBooksInCategory = (category) => {
      return readBooks.value.filter(book => book.tags.includes(category))
    }

    return {
      isListView,
      toggleView,
      navigateToBookDetails,
      currentlyReadingBooks,
      readBooks,
      wantToReadBooks,
      readCategories,
      getBooksInCategory
    }
  }
}
</script>

